/**
 * Element  CSS class 
 */
export default class Classes {
  // ----------------------------------------
  // STATIC METHOD
  // ----------------------------------------
  /**
   *  IE , `.classList` 
   * @param {Element} element  NodeList
   * @returns {Array} 
   */
  static convert(element) {
    const arr = element.className.split(' ');
    let i = 0;
    const limit = arr.length;
    const empty = [];
    for (;i < limit; i += 1) {
      const className = arr[i];
      if (!!className && className !== ' ') {
        empty.push(className);
      }
    }
    return empty;
  }

  /**
   *  `element`  class name list 
   * 1. `classList` && `Array.from` - `Array.from(element.classList)`
   * 2. {@link Classes.convert}
   * @param {Element} element  Element
   * @returns {Array.<string>}  `element`  class 
   */
  static get(element) {
    return element.classList && Array.from
      ? Array.from(element.classList)
      : Classes.convert(element);
  }

  /**
   * Element className 
   * @param {Element} element  Element
   * @param {string} className  CSS class name
   * @returns {boolean}  true 
   */
  static has(element, className) {
    return element.classList ?
      element.classList.contains(className) :
      Classes.get(element).includes(className);
    // const elementClasses = Classes.get(element);
    // return elementClasses.indexOf(className) !== -1;
  }

  /**
   * Element  className 
   * - className 
   * -
   * @param {Element} node  Element
   * @param {string} className  CSS class name
   * @returns {boolean}  true 
   */
  static add(node, className) {
    if (node.classList) {
      node.classList.add(className);
      return true;
    }
    // CSS class 
    if (Classes.has(node, className)) {
      return false;
    }
    // argument copy
    const element = node;
    // @type {Array<string>} - element class 
    const elementClasses = Classes.get(element);
    //  `className` 
    elementClasses.push(className);
    //  ' '
    element.className = elementClasses.join(' ');
    return true;
  }

  /**
   * Element  className 
   * @param {Element} node  Element
   * @param {string} className  CSS class name
   * @returns {boolean}  true 
   */
  static remove(node, className) {
    if (node.classList) {
      node.classList.remove(className);
      return true;
    }
    if (!Classes.has(node, className)) {
      return false;
    }
    // argument copy
    const element = node;
    // @type {Array<string>} - element class 
    const elementClasses = Classes.get(element);
    //  class 
    const index = elementClasses.indexOf(className);
    // 
    elementClasses.splice(index, 1);
    //  ' '
    element.className = elementClasses.join(' ');
    return true;
  }

  // ----------------------------------------
  // CONSTRUCTOR
  // ----------------------------------------
  /**
   *  Element 
   * @param {Element} element  Element
   */
  constructor(element) {
    /**
     *  Element
     * @type {Element}
     */
    this.element = element;
  }

  // ----------------------------------------
  // METHOD
  // ----------------------------------------
  /**
   * className 
   * @param {string} className  CSS class name
   * @returns {boolean}  true 
   */
  has(className) {
    return Classes.has(this.element, className);
  }

  /**
   * className 
   * @param {string} className  CSS class name
   * @returns {boolean}  true 
   */
  add(className) {
    return Classes.add(this.element, className);
  }

  /**
   * className 
   * @param {string} className  CSS class name
   * @returns {boolean}  true 
   */
  remove(className) {
    return Classes.remove(this.element, className);
  }
}
