/**
 * (x, y)(time)
 */
export default class Vectors {
  /**
   * 
   * @param {number} [x=0]  x
   * @param {number} [y=0]  y
   * @param {number} [time=Date.now()]  milli seconds
   */
  constructor(x = 0, y = 0, time = Date.now()) {
    /**
     *  x
     * @type {number}
     */
    this.x = x;
    /**
     *  y
     * @type {number}
     */
    this.y = y;
    /**
     *  milli seconds
     * @type {number}
     */
    this.time = time;
    /**
     * , true: 
     * @type {boolean}
     */
    this.scrolling = false;
  }

  /**
   * x, y, time  `0` 
   * @returns {Vectors} 
   */
  reset() {
    this.x = 0;
    this.y = 0;
    this.time = 0;

    return this;
  }

  /**
   * x, y, time 
   * @param {number} x  x
   * @param {number} y  y
   * @param {number} [time=Date.now]  milli seconds
   * @returns {Vectors} 
   */
  update(x, y, time = Date.now()) {
    this.x = x;
    this.y = y;
    this.time = time;

    return this;
  }

  /**
   *  vectors 
   * @param {Vectors} vectors  Vector instance
   * @returns {number} 
   */
  distance(vectors) {
    const distanceX = this.x - vectors.x;
    const distanceY = this.y - vectors.y;
    const sqrt = (distanceX * distanceX) + (distanceY * distanceY);
    return Math.sqrt(sqrt);
  }

  /**
   *  vectors 
   * @param {Vectors} vectors  Vector instance
   * @returns {number} (milli seconds) 
   */
  duration(vectors) {
    return this.time - vectors.time;
  }

  /**
   * 
   * @returns {Vectors} 
   */
  clone() {
    const clone = new Vectors(this.x, this.y, this.time);
    clone.scrolling = this.scrolling;
    return clone;
  }

  /**
   * 
   * @returns {number} 
   */
  length() {
    const { x, y } = this;
    return Math.sqrt((x * x) + (y * y));
  }

  /**
   *  scalar 
   *
   * @param {number} scalar 
   * @returns {Vectors}  Vector 
   */
  divideByScalar(scalar) {
    const clone = this.clone();
    if (scalar === 0) {
      clone.x = 0;
      clone.y = 0;
    } else {
      const { x, y } = clone;
      const inverse = 1 / scalar;
      clone.x = x * inverse;
      clone.y = y * inverse;
    }

    return clone;
  }

  /**
   *  scalar 
   *
   * @param {number} scalar 
   * @returns {Vectors}  Vector 
   */
  multiplyByScalar(scalar) {
    const clone = this.clone();
    clone.x *= scalar;
    clone.y *= scalar;
    return clone;
  }

  /**
   *  Vectors  `maxValue`  `Vectors` 
   * @param {number} maxValue 
   * @returns {Vectors} `maxValue`  `Vectors` 
   */
  truncate(maxValue) {
    const minValue = Math.min(maxValue, this.length());
    const oldLength = this.length();
    if (oldLength !== 0 && minValue !== oldLength) {
      return this.multiplyByScalar(minValue / oldLength);
    }
    return this.clone();
  }

  /**
   * 1 Vector 
   * @returns {Vectors} 1 Vectors 
   */
  normalize() {
    return this.divideByScalar(this.length());
  }

  /**
   * length  1
   * @returns {boolean} true: 
   */
  isNormalize() {
    return this.length() === 1;
  }

  /**
   *  vectors  X 
   * @param {Vectors} vectors  Vectors instance
   * @returns {number} X 
   */
  betweenX(vectors) {
    return this.x - vectors.x;
  }

  /**
   *  vectors  Y 
   * @param {Vectors} vectors  Vectors instance
   * @returns {number} Y 
   */
  betweenY(vectors) {
    return this.y - vectors.y;
  }

  /**
   *  vectors  time 
   * @param {Vectors} vectors  Vectors instance
   * @returns {number} time 
   */
  betweenTime(vectors) {
    return this.time - vectors.time;
  }

  /**
   *  vectors  x, y, time 
   * @param {Vectors} vectors  Vectors instance
   * @returns {Vectors}  vectors  x, y, time  clone 
   */
  between(vectors) {
    const clone = this.clone();
    clone.x = clone.betweenX(vectors);
    clone.y = clone.betweenY(vectors);
    clone.time = clone.betweenTime(vectors);

    return clone;
  }

  /**
   * 
   * @param {Vectors} vectors  Vectors instance
   * @returns {number} 
   */
  dot(vectors) {
    return (this.x * vectors.x) + (this.y * vectors.y);
  }

  /**
   * (x, y)
   * @param {Vectors} vectors  Vectors instance
   * @returns {boolean} true: 
   */
  equals(vectors) {
    return (vectors.x === this.x) && (vectors.y === this.y);
  }

  /**
   * 
   * @param {Vectors} vectors  Vectors instance
   * @returns {number} 
   */
  angle(vectors) {
    let v1 = this.clone();
    let v2 = vectors.clone();
    if (!v1.isNormalize()) {
      v1 = v1.normalize();
    }
    if (!v2.isNormalize()) {
      v2 = v2.normalize();
    }
    // console.log('angle', v1, v2, v1.dot(v2), Math.acos(v1.dot(v2)));
    return Math.acos(v1.dot(v2));
  }
}
