// events
import Events from '../Events';

// util
import Vectors from '../../util/Vectors';

/**
 * {@link Touching} Events
 */
export default class TouchingEvents extends Events {
  /**
   * Touching events object 
   * @param {string} type event type
   * @param {*} target 
   * @param {Event} origin 
   * @param {Vectors} current 
   * @param {Vectors} between 
   * @param {boolean} scrolling scroll , true: scroll 
   */
  constructor(
    type,
    target,
    origin,
    current = new Vectors(),
    between = new Vectors(),
    scrolling = false,
  ) {
    // super
    super(type, target);
    /**
     * 
     * @type {Event}
     */
    this.origin = origin;
    /**
     * 
     * @type {Vectors}
     */
    this.current = current;
    /**
     * 
     * @type {Vectors}
     */
    this.between = between;
    /**
     * scroll 
     * @type {boolean}
     */
    this.scrolling = scrolling;
  }
}
