import Text from '../util/Text';

/**
 * URL query 
 */
export default class Queries {
  // ----------------------------------------
  // STATIC METHOD
  // ----------------------------------------
  /**
   * `&amp;`  `&` 
   * @param {string} targetText 
   * @returns {string} `&amp;`  `&` 
   */
  static amp(targetText) {
    return targetText.split('&amp;').join('&');
  }
  /**
   *  `?`  {@link Text.and}  `&amp;`  `&` 
   * @param {string} targetText 
   * @returns {string} query 
   */
  static naked(targetText) {
    // const queryString = Queries.amp(targetText);
    const queryString = Text.and(targetText);
    return queryString.substr(0, 1) === '?' ? queryString.substring(1) : targetText;
  }

  /**
   * query  kye: value 
   * @param {string} targetText 
   * @returns {[Object, Array]} data, keys 
   */
  static parse(targetText) {
    const query = Queries.naked(targetText);
    const pairs = query.split('&');
    const data = {};
    const keys = [];
    pairs.map((pair) => {
      let keyName = '';
      if (pair && pair.indexOf('=') !== -1) {
        // @type {Array<string>} - `key=value`  `=` 
        const keyValue = pair.split('=');
        // @type {string} keyName
        const key = keyValue.shift();
        // data object  keyName  key 
        data[key] = keyValue.shift();
        keyName = key;
        // key 
        keys.push(key);
      }
      return keyName;
    });

    return [data, keys];
  }

  /**
   *  targetText (query)  keyName 
   * @param {string} keyName key 
   * @param {string} targetText query
   * @returns {string|undefined} ,  undefined 
   */
  static get(keyName, targetText = window.location.search) {
    const [data] = Queries.parse(targetText);
    return data[keyName];
  }

  /**
   * URL query  key: value 
   * @param {string} targetText query
   * @returns {[Object, Array]} URL query  key: value 
   */
  static getAll(targetText = window.location.search) {
    // const [data] = Queries.parse(targetText);
    return Queries.parse(targetText);
  }

  // ----------------------------------------
  // CONSTRUCTOR
  // ----------------------------------------
  /**
   * URL query 
   * @param {string} [queryString=location.search]  URL 
   */
  constructor(queryString = window.location.search) {
    const [data, keys] = Queries.parse(queryString);
    const naked = Queries.naked(queryString);
    /**
     * query key  - query key array
     * @type {Array<string>}
     */
    this.keys = keys;
    /**
     * key: value  - URL query  key: value 
     * @type {Object}
     */
    this.data = data;
    /**
     * query  -  query 
     * @type {string}
     */
    this.queryString = queryString;
    /**
     *  query  - `?`  + `&amp;`  `&` 
     * @type {string}
     */
    this.naked = naked;
  }

  // ----------------------------------------
  // METHOD
  // ----------------------------------------
  /**
   * key 
   * @param {string} keyName  key 
   * @returns {boolean} true: 
   */
  has(keyName) {
    return this.keys.indexOf(keyName) !== -1;
  }

  /**
   * key 
   * @param {string} keyName  key 
   * @returns {string|undefined} ,  undefined 
   */
  get(keyName) {
    return this.data[keyName];
  }

  /**
   * key: value 
   * @returns {Object} URL query  key: value 
   */
  getAll() {
    return this.data;
  }
}
