// event
import EventDispatcher from '../event/EventDispatcher';
// import Events from '../event/Events';

// tick
import Cycle from './Cycle';

// tick/events
import PollingEvents from './events/PollingEvents';
// import CycleEvents from './events/CycleEvents';

/**
 *  UPDATE 
 *
 * @example
 * // 3sec. interval
 * const polling = new Polling(1000 * 3);
 * const update = () => {
 *  // code here, something do
 * };
 * polling.on(Polling.UPDATE, update);
 * polling.start();
 */
export default class Polling extends EventDispatcher {
  // ----------------------------------------
  // STATIC EVENT
  // ----------------------------------------
  /**
   * (milliseconds) - pollingUpdate
   * @event UPDATE
   * @type {string}
   */
  static UPDATE = 'pollingUpdate';

  // ----------------------------------------
  // CALLBACK
  // ----------------------------------------
  /**
   * Cycle.UPDATE event handler, polling 
   *
   * @param {CycleEvents} events Cycle event object
   * @listens {Cycle.UPDATE} Cycle.UPDATE 
   * @returns {boolean} Polling.UPDATE event  true 
   */
  onUpdate = (events) => {
    // 
    // @type {number}
    const present = Date.now();
    // @type {number} - interval 
    // const interval = this.interval;
    // @type {number} - 
    const { begin, interval } = this;
    //   interval 
    if ((present - begin) >= interval) {
      // event 
      this.fire(this.updateEvents(begin, present, events));
      //  update
      this.begin = present;
      // event 
      return true;
    }
    return false;
  };

  // ----------------------------------------
  // CONSTRUCTOR
  // ----------------------------------------
  /**
   *  polling  UPDATE 
   * @param {number} [interval=1000]  milliseconds
   */
  constructor(interval = 1000) {
    super();
    /**
     * Cycle instance 
     * @ty[e {Cycle}
     */
    this.cycle = Cycle.factory();
    /**
     * (ms)
     * @type {number}
     */
    this.interval = interval;
    // /**
    //  * bound onUpdate, Cycle.UPDATE event handler
    //  * @returns {function}
    //  */
    // this.onUpdate = this.onUpdate.bind(this);
    /**
     * Events instance - Polling.UPDATE Events object
     * @type {Events}
     */
    this.events = new PollingEvents(Polling.UPDATE, this, this);
    /**
     * polling event , event  0 
     * @type {number}
     */
    this.begin = 0;
  }

  // ----------------------------------------
  // METHOD
  // ----------------------------------------
  /**
   * polling <br>
   * 1.  polling 
   * 1. update 
   * @param {number} interval polling 
   * @returns {boolean} `update`  Polling.UPDATE event  true 
   */
  change(interval) {
    this.interval = interval;
    return this.start();
  }

  /**
   * polling 
   * @returns {boolean} start  true 
   */
  start() {
    // event unbind
    this.stop();
    // @type {number} - 
    this.begin = Date.now();
    this.cycle.on(Cycle.UPDATE, this.onUpdate);
    return true;
  }

  /**
   * polling 
   * @returns {boolean} stop  true 
   */
  stop() {
    this.cycle.off(Cycle.UPDATE, this.onUpdate);
    return true;
  }

  // -----
  // event create & fire
  /**
   * events object 
   * @param {number} begin : 
   * @param {number} present 
   * @param {CycleEvents} cycleEvents Cycle event object
   * @returns {Events}  Events 
   */
  updateEvents(begin, present, cycleEvents) {
    this.begin = begin;
    // @type {Events} - start event
    const { events } = this;
    events.begin = begin;
    events.present = present;
    events.interval = this.interval;
    events.cycleEvents = cycleEvents;
    return events;
  }

  /**
   * Polling.UPDATE event 
   * @param {Events} events Polling.UPDATE event object
   * @returns {void}
   */
  fire(events) {
    this.dispatch(events);
  }
}
