// event
import EventDispatcher from './EventDispatcher';
import ScrollEvents from './events/ScrollEvents';

// util
import Freeze from '../util/Freeze';

/**
 * new  inner Symbol
 * @type {symbol}
 * @private
 */
const singletonSymbol = Symbol('Scroll singleton symbol');

/**
 * singleton instance, nullable
 * @type {?Scroll}
 * @private
 */
let instance = null;

/**
 * window scroll event 
 * - singleton  new  factory  instance 
 *
 * @example
 *  const instance = Scroll.factory();
 */
export default class Scroll extends EventDispatcher {
  // ----------------------------------------
  // STATIC CONST
  // ----------------------------------------
  /**
   * scroll  - `scrollScroll`
   * @event SCROLL
   * @type {string}
   */
  static SCROLL = 'scrollScroll';

  // ----------------------------------------
  // STATIC METHOD
  // ----------------------------------------
  /**
   * Scroll instance  singleton 
   * @returns {Scroll} Scroll instance 
   */
  static factory() {
    if (instance === null) {
      instance = new Scroll(singletonSymbol);
    }
    return instance;
  }

  /**
   * y  scroll top 
   * @param {number} [y=0] scroll top 
   * @param {number} [delay=0] time out  ms
   * @returns {number} time out id
   */
  static jump(y = 0, delay = 0) {
    return setTimeout(() => { window.scrollTo(0, y); }, delay);
  }

  /**
   * {@link Freeze}.freeze  scroll 
   * @returns {number} time out ID
   */
  static freeze() {
    return Freeze.freeze();
  }

  /**
   * scroll top 
   * @returns {number} scroll top 
   * @see https://developer.mozilla.org/ja/docs/Web/API/Window/scrollY
   * @see https://developer.mozilla.org/en-US/docs/Web/API/Window/pageYOffset
   */
  static y() {
    return (typeof window.pageYOffset !== 'undefined')
      ? window.pageYOffset
      : (document.documentElement
        || document.body.parentNode
        || document.body).scrollTop;
  }

  // ---------------------------------------------------
  //  CALLBACK
  // ---------------------------------------------------
  /**
   * window scroll event handler
   * - window scroll event  scroll top  Scroll.SCROLL custom event 
   * @param {?Event} event window scroll event, nullable
   */
  onScroll = (event) => {
    // @type {number} - scroll top
    const y = Scroll.y();
    // @type {number} - window height
    const { innerHeight } = window;
    // @type {number} -  scroll top
    // @type {Events} - events
    const { events, previous } = this;
    // @type {Event} - scroll event
    events.original = event;
    // @type {number} - scroll top
    events.y = y;
    // @type {number} - window height
    events.height = innerHeight;
    // @type {number} - window bottom(y + height)
    events.bottom = y + innerHeight;
    events.previous = previous;
    // @type {boolean} -  scroll top , true: 
    events.changed = previous !== y;
    // @type {number} -  +: down, -: up
    events.moving = y - previous;
    // event fire
    this.dispatch(events);
    this.previous = y;
  };

  // ---------------------------------------------------
  //  CONSTRUCTOR
  // ---------------------------------------------------
  /**
  /**
   * singleton 
   * @param {symbol} checkSymbol singleton  private instance
   * @returns {Scroll} singleton instance 
   */
  constructor(checkSymbol) {
    // checkSymbol  singleton 
    if (checkSymbol !== singletonSymbol) {
      throw new Error('don\'t use new, instead use static factory method.');
    }
    // instance  instance  null  this 
    if (instance !== null) {
      return instance;
    }
    // onetime setting
    super();
    // instance = this;

    // event handler
    // /**
    //  * bound onScroll, window.onscroll event handler
    //  * @type {function}
    //  */
    // this.onScroll = this.onScroll.bind(this);
    /**
     * ScrollEvents instance, 
     * @type {ScrollEvents}
     */
    this.events = new ScrollEvents(Scroll.SCROLL, this, this);
    /**
     *  scroll top 
     * @type {number}
     * @default -1
     */
    this.previous = -1
    //  instance 
    return this;
  }

  // ----------------------------------------
  // METHOD
  // ----------------------------------------
  /**
   * scroll event 
   * @returns {Scroll} method chain  instance 
   */
  start() {
    this.stop();
    window.addEventListener('scroll', this.onScroll, false);
    return this;
  }

  /**
   * scroll event 
   * @returns {Scroll} method chain  instance 
   */
  stop() {
    window.removeEventListener('scroll', this.onScroll);
    return this;
  }
}
