// util
import Type from '../util/Type';
import Text from '../util/Text';

// css
import Patterns from './Patterns';

/**
 * Element  style 
 */
export default class Style {
  // ----------------------------------------
  // STATIC METHOD
  // ----------------------------------------
  /**
   * element style ,
   * [getComputedStyle](https://developer.mozilla.org/en-US/docs/Web/API/Window/getComputedStyle) 
   * @see https://developer.mozilla.org/en-US/docs/Web/API/Window/getComputedStyle
   * @param {Object|Window} view Document.defaultView
   * @param {Element} element  Element
   * @param {string} [property='']  CSS property name,  `CSSStyleDeclaration` 
   * @returns {CSSStyleDeclaration|CssStyle|string|undefined} style value 
   * @see https://developer.mozilla.org/en-US/docs/Web/API/Window/getComputedStyle
   * @see https://developer.mozilla.org/en-US/docs/Web/API/Document/defaultView
   */
  static compute(view, element, property = '') {
    const style = view.getComputedStyle(element, null);
    if (Type.exist(property)) {
      const props = property.replace(/([A-Z])/g, '-$1').toLowerCase();
      return style.getPropertyValue(props);
    }
    return style;
  }

  /**
   * CSS  short hand  {@link Patterns} 
   * @param {Object|Window} view Document.defaultView
   * @param {Element} element  Element
   * @param {Array<string>} patterns  CSS property name 
   * @returns {*|string|undefined} style value 
   * @see https://developer.mozilla.org/en-US/docs/Web/API/Document/defaultView
   */
  static shortHand(view, element, patterns) {
    const top = Style.compute(view, element, patterns[0]);
    const right = Style.compute(view, element, patterns[1]);
    const bottom = Style.compute(view, element, patterns[2]);
    const left = Style.compute(view, element, patterns[3]);
    if (!top && !right && !bottom && !left) {
      return undefined;
    }
    // top / bottom ...
    if (top === bottom) {
      // top - bottom: same
      if (right === left) {
        // top - bottom: same
        if (top === right) {
          // right - left: same - all same
          return top;
        }
        // top-bottom, left-right
        return `${top} ${right}`;
      }
      // separate 4
      return `${top} ${right} ${bottom} ${left}`;
    }
    // right / left
    if (right === left) {
      // top - bottom: different, left- right: same
      return `${top} ${right} ${bottom}`;
    }
    // separate 4
    return `${top} ${right} ${bottom} ${left}`;
  }

  /**
   *  `element`  css 
   * @param {Element} element  Element
   * @param {string} css `cssText` 
   */
  static change(element, css) {
    const { style } = element;
    style.cssText = css;
  }

  // ----------------------------------------
  // CONSTRUCTOR
  // ----------------------------------------
  /**
   *  element  style 
   * @param {?Element} element  Element
   */
  constructor(element) {
    /**
     *  Element
     * @type {Element}
     */
    this.element = element;
    // @type {string} -  element.style.cssText 
    const css = this.current();
    /**
     *  inline CSS
     * @type {string}
     */
    this.css = css;
    /**
     *  inline CSS
     * @type {string}
     */
    this.original = css;
  }

  // ----------------------------------------
  // METHOD
  // ----------------------------------------
  /**
   *  inline CSS 
   * @param {string} style  CSS 
   * @returns {string}  CSS
   */
  update(style) {
    this.css = style;
    return style;
  }

  /**
   * style value 
   * @param {string} [property='']  style property name
   * @return {string} style value 
   */
  get(property = '') {
    const { element } = this;
    const { ownerDocument } = element;
    const { defaultView } = ownerDocument;
    let style = Style.compute(defaultView, element, property);
    // firefox  css shorthand 
    if (style === '' && property && Patterns.match(property)) {
      style = Style.shortHand(defaultView, element, Patterns.get(property));
    }
    return style;
  }

  /**
   * element  css  css 
   * @param {string} property  css property name
   * @param {string} value  css value
   * @return {boolean}  true 
   */
  set(property, value) {
    // 
    const { element } = this;
    if (!Type.exist(element)) {
      return false;
    }
    // 
    const prop = Text.camel(property);
    element.style[prop] = value;

    return true;
  }

  /**
   * element  inline style(style.cssText) 
   * @return {string} style.cssText 
   */
  current() {
    const { element } = this;
    if (Type.exist(element)) {
      return element.style.cssText;
    }

    // this.element 
    return '';
  }

  /**
   * element  style.cssText 
   * @return {string}  css 
   */
  restore() {
    const css = this.original;
    this.element.style.cssText = css;
    return css;
  }

  /**
   * `save`  inline style  default 
   * @returns {string}  inline style 
   */
  save() {
    const style = this.current();
    return this.update(style);
  }

  /**
   * element  style.cssText  `css` 
   * @param {string} css  css
   */
  change(css) {
    Style.change(this.element, css);
  }
}
