/**
 * 
 * @static
 */
export default class Type {
  /**
   * (target)
   * @param {Function|*} target 
   * @returns {boolean} (target)true: 
   */
  static method(target) {
    return typeof target === 'function';
  }

  /**
   * (target) `!!` 
   * @param {*} target 
   * @returns {boolean} (target) `!!` 
   */
  static exist(target) {
    return !!target;
  }

  /**
   * (target) number 
   * @param {*|string} target 
   * @returns {boolean} (target) number true: number
   */
  static number(target) {
    // [] jQuery 2.x, jQuery 2  "2"  true  type check 
    return typeof target === 'number'
      && !Type.array(target)
      && ((target - parseFloat(target)) + 1) >= 0;
  }

  /**
   * (target) int  `Number.isInteger` 
   * @param {*} target 
   * @returns {boolean} (target) int true: int
   */
  static int(target) {
    return Number.isInteger(target);
  }

  /**
   * (target) string 
   * @param {*} target 
   * @returns {boolean} (target) string true: string
   */
  static string(target) {
    return typeof target === 'string';
  }

  /**
   * (target) `Array.isArray` 
   * @param {*} target 
   * @returns {boolean} (target)  true: 
   */
  static array(target) {
    return Array.isArray(target);
  }

  /**
   * (target) null 
   * @param {*} target 
   * @returns {boolean} (target) null true: null
   */
  static nil(target) {
    return target === null;
  }

  /**
   * Object  `object`  String  `key`  [key] 
   * @deprecated instead use Type.has
   * @param {Object} target 
   * @param {string} key Object.key 
   * @returns {boolean}  true 
   */
  static hasKey(target, key) {
    return Type.has(target, key);
  }

  /**
   * Object  `object`  String  `key`  [key] 
   * @param {Object} target 
   * @param {string} key Object.key 
   * @returns {boolean}  true 
   */
  static has(target, key) {
    // return Object.keys(target).indexOf(key) !== -1;
    return Object.keys(target).includes(key);
  }

  /**
   * target  undefined 
   * @param {*} target 
   * @returns {boolean} true: undefined
   * @since 2016-10-25
   */
  static undef(target) {
    return typeof target === 'undefined';
  }

  /**
   * 
   * @deprecated instead use Type.extension
   * @param {string} fileName 
   * @returns {string} 
   */
  static getExtension(fileName) {
    return Type.extension(fileName);
  }

  /**
   * 
   * @param {string} fileName 
   * @returns {string} 
   */
  static extension(fileName) {
    if (typeof fileName !== 'string') {
      return '';
    }
    const splits = fileName.split('.');
    if (splits.length === 1) {
      return '';
    }
    return splits.pop().toLowerCase();
  }

  // ----------------------------------------------------------
  // 
  /**
   * base64 file 
   * @param {string} fileName 
   * @returns {boolean} jpeg / png  true 
   */
  static base64(fileName) {
    if (!Type.exist(fileName)) {
      return false;
    }
    return fileName.indexOf('data:image/jpeg;base64') !== -1
      || fileName.indexOf('data:image/png;base64') !== -1
      || fileName.indexOf('data:image/jpg;base64') !== -1
      || fileName.indexOf('data:image/gif;base64') !== -1;
  }

  /**
   * 
   * @param {string} fileName 
   * @returns {Boolean} 'jpg', 'png', 'jpeg', 'gif' 
   */
  static img(fileName) {
    if (!Type.exist(fileName)) {
      return false;
    }
    // base64
    if (Type.base64(fileName)) {
      return true;
    }
    // 
    return ['jpg', 'png', 'jpeg', 'gif'].indexOf(Type.extension(fileName)) !== -1;
  }
}
