// event
import Events from '../event/Events';

// tick
import Polling from './Polling';

/**
 *  fps 
 *
 * 
 *
 * - 60: Rate.RATE_60
 * - 30: RATE_30
 * - 20: RATE_20
 * - 15: RATE_15
 * - 12: RATE_12
 * - 10: RATE_10
 * - 6: RATE_6
 * - 5: RATE_5
 *
 * @example
 * // 1sec interval
 * const rate = new Rate(Rate.Rate_60);
 * const update = () => {
 *  // code here, something do
 * };
 * rate.on(Rate.UPDATE, update);
 * rate.start();
 */
export default class Rate extends Polling {
  // ----------------------------------------
  // CONST
  // ----------------------------------------
  /**
   * fps 60 
   * @type {number}
   */
  static RATE_60 = 1;

  /**
   * fps 30 
   * @type {number}
   */
  static RATE_30 = 2;

  /**
   * fps 20 
   * @type {number}
   */
  static RATE_20 = 3;

  /**
   * fps 15 
   * @type {number}
   */
  static RATE_15 = 4;

  /**
   * fps 12 
   * @type {number}
   */
  static RATE_12 = 5;

  /**
   * fps 10 
   * @type {number}
   */
  static RATE_10 = 6;

  /**
   * fps 6 
   * @type {number}
   */
  static RATE_6 = 10;

  /**
   * fps 5 
   * @type {number}
   */
  static RATE_5 = 12;

  /**
   * fps 4 
   * @type {number}
   */
  static RATE_4 = 15;

  /**
   * fps 3 
   * @type {number}
   */
  static RATE_3 = 20;

  /**
   * fps 2 
   * @type {number}
   */
  static RATE_2 = 30;

  /**
   * fps 1 
   * @type {number}
   */
  static RATE_1 = 60;

  // ----------------------------------------
  // EVENT
  // ----------------------------------------
  /**
   *  - rateUpdate
   * @event UPDATE
   * @type {string}
   */
  static UPDATE = 'rateUpdate';

  /**
   * {@link Cycle}.UPDATE event handler
   *
   * count property  `+1`  rate  `0`  Rate.UPDATE 
   * @param {CycleEvents} events Polling event object
   * @returns {boolean} Rate.UPDATE event  true 
   */
  onUpdate = (events) => {
    //  0 
    this.count += 1;
    const reminder = this.count % this.rate;
    if (reminder === 0) {
      this.count = 0;
      this.fire(this.updateEvents(0, 0, events));
      return true;
    }
    return false;
  };

  // ----------------------------------------
  // CONSTRUCTOR
  // ----------------------------------------
  /**
   *  UPDATE 
   * @param {number} [rateValue=Rate.RATE_5] fps, 
   */
  constructor(rateValue = Rate.RATE_5) {
    // 60fps  polling 
    super(1000 / 60);
    // @type {Events}
    const events = new Events(Rate.UPDATE, this, this);
    //  rate list
    const rates = [
      Rate.RATE_60,
      Rate.RATE_30,
      Rate.RATE_20,
      Rate.RATE_15,
      Rate.RATE_12,
      Rate.RATE_10,
      Rate.RATE_6,
      Rate.RATE_5,
      Rate.RATE_4,
      Rate.RATE_3,
      Rate.RATE_2,
      Rate.RATE_1,
    ];
    /**
     * Rate  Events instance
     * @type {Events}
     */
    this.events = events;
    /**
     *  rate
     * @type {Array<number>}
     */
    this.rates = rates;
    /**
     * rate count, update <br>
     *  `Rate.RATE_5` 
     * @type {number}
     */
    this.count = 0;
    // let rate = this.validate(rateValue) ? rateValue : Rate.RATE_5;
    /**
     * rate 
     * @type {?number}
     * @default Rate.RATE_5
     */
    this.rate = this.validate(rateValue) ? rateValue : Rate.RATE_5;
  }

  // ----------------------------------------
  // METHOD
  // ----------------------------------------
  /**
   * rate 
   * - : `this.rate` , value 
   * - : `this.rate` , null 
   * @param {number} value rate 
   * @returns {?number}  rate  null 
   */
  setRate(value) {
    if (this.validate(value)) {
      this.rate = value;
      return value;
    }
    return null;
  }

  /**
   *  rate 
   * @param {number} rate  rate
   * @returns {boolean}  true 
   */
  validate(rate) {
    return this.rates.indexOf(rate) !== -1;
  }

  /**
   * fps 
   * @throws {Error}  rate 
   * @param {number} rate fps , <br>
   * RATE_60, RATE_30, RATE_20, RATE_15, RATE_12, RATE_10, <br>
   * RATE_6, RATE_5, RATE_4, RATE_3, RATE_2, RATE_1 
   * @returns {boolean} rate  true 
   */
  change(rate) {
    this.setRate(rate);
    return this.start();
  }
}
