// util
import Type from '../util/Type';

// /**
//  * private property key, listeners Object
//  * @type {Symbol}
//  * @private
//  */
// const listenersKey = Symbol('event listeners object');

/**
 * Custom Event  Event 
 *
 * ```
 * const callback = (event) => {
 *  console.log(event);
 * };
 *
 * const event = new EventDispatcher();
 * event.on('abc', callback);
 *
 * console.log(event.has('abc', callback));// true
 *
 * event.dispatch(new Events('abc'));
 *
 * event.off('abc', callback);
 * console.log(event.has('abc', callback));// false
 * ```
 *
 * {@link Events}
 */
export default class EventDispatcher {
  /**
   * listener property 
   */
  constructor() {
    /**
     *  object,
     * event type {string}  key,  Array.<function> 
     * @type {Object}
     */
    this.listeners = {...{}};
    // this.listeners = Object.create({});
  }

  // ----------------------------------------
  // METHOD
  // ----------------------------------------
  /**
   * 
   */
  destroy() {
    // this.listeners = Object.create({});
    this.listeners = {...{}};
  }

  /**
   * event type   bind 
   * @param {string} type event type
   * @param {Function} listener callback
   * @returns {boolean} 
   */
  on(type, listener) {
    if (!Type.exist(type)) {
      // type 
      return false;
    }
    if (!Type.method(listener)) {
      // listener  
      return false;
    }

    // type {Object} - {{eventType: array [listener: Function...]...}}
    const { listeners } = this;

    if (!Type.has(listeners, type)) {
      // listeners.type 
      // listeners.type 
      // listener {function} 
      listeners[type] = [];
    }
    //  listeners.type 
    listeners[type].push(listener);
    return true;
  }

  /**
   * event type  remove 
   * -  null 
   * - {@link EventDispatcher.clean}  all null 
   * @param {string} type event type
   * @param {Function} listener 
   * @returns {boolean} 
   */
  off(type, listener) {
    if (!Type.method(listener)) {
      // listener  
      return false;
    }

    // @type {Object} - events.type:String: [listener:Function...]
    const { listeners } = this;
    if (!Type.has(listeners, type)) {
      // listener.type  - 
      return false;
    }

    // @type {Array} - listener list
    const types = listeners[type];

    // listener 
    // @type {number}
    const index = types.indexOf(listener);

    if (index === -1) {
      //  -1, 
      // 
      return false;
    }

    //  null 
    // loop(iterator)  off  index 
    types[index] = null;

    this.clean(type, types);

    return true;
  }

  /**
   * 
   * -  null  
   * @param {string} type event type
   * @param {Array<Function>} types event type 
   * @returns {boolean} , true: 
   */
  clean(type, types) {
    // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Global_Objects/Array/some
    // Array.some   true 
    // types  null 
    // @type {boolean} - listener list  (typeof 'function') true 
    const hasFunction = types.some(listener => Type.method(listener));

    if (!hasFunction) {
      // null 
      // this.listeners[type] = [].slice(0);
      this.listeners[type] = [...[]];
    }

    //  hasFunction flag 
    return !hasFunction;
  }

  /**
   * event type 
   * @param {string} type event type
   * @param {Function} listener 
   * @returns {boolean} event type 
   */
  has(type, listener) {
    if (!Type.method(listener)) {
      // listener  
      return false;
    }

    // @type {Object} - events.type:String: [listener:Function...]
    const { listeners } = this;

    if (!Type.has(listeners, type)) {
      // listener.type  - 
      return false;
    }

    // @type {boolean} - 
    // return listeners[type].indexOf(listener) !== -1;
    return listeners[type].includes(listener);
  }

  /**
   *  call 
   * @param {Events|*} events  Event Object.type dispatch  target  this 
   * @returns {boolean} 
   */
  dispatch(events) {
    // @type {Object} - events.type:string: [listener:Function...]
    const { listeners } = this;
    // @type {string} - event type
    const { type } = events;

    if (!Type.has(listeners, type)) {
      // listener.type  - 
      return false;
    }

    // event.target = this 
    // Assignment to property of function parameter 'event'  no-param-reassign
    // 
    const eventObject = events;
    // target 
    eventObject.target = this;

    // callback 
    listeners[type]
      .map(
        // @param listener {Function}
        (listener) => {
          // null  listener 
          if (Type.method(listener)) {
            return listener.call(this, eventObject);
          }
          return null;
        },
      );

    return true;
  }

  /**
   * **alias on**
   * - event type   bind 
   * @deprecated instead use {@link EventDispatcher.on}
   * @param {string} type event type
   * @param {Function} listener callback
   * @returns {boolean} 
   */
  addEventListener(type, listener) {
    return this.on(type, listener);
  }

  /**
   * **alias off**
   * - event type  remove 
   * @deprecated instead use {@link EventDispatcher.off}
   * @param {string} type event type
   * @param {Function} listener 
   * @returns {boolean} 
   */
  removeEventListener(type, listener) {
    return this.off(type, listener);
  }

  /**
   * **alias has**
   * - event type 
   * @deprecated instead use {@link EventDispatcher.has}
   * @param {string} type event type
   * @param {Function} listener 
   * @returns {boolean} event type 
   */
  hasEventListener(type, listener) {
    return this.has(type, listener);
  }

  /**
   * **alias dispatch**
   * -  call 
   * @deprecated instead use {@link EventDispatcher.dispatch}
   * @param {Events} events type 
   * @returns {boolean} 
   */
  dispatchEvent(events) {
    return this.dispatch(events);
  }
}
